.choose {
  background: white;
  border-radius: 10px;
  position: absolute;
  padding: 10px;
  list-style: none;
  display: none;
  margin: 0;
  max-height: 200px;
    overflow-y: scroll;
}

.choose_active {
  display: block;
}

.chooseField {
  cursor: pointer;
  margin: 10px 0;
}

.chooseField:hover {
  background: #f5f7ff;
  color: #7671dd;
  box-shadow: 0 6px 18px 0 rgb(54 55 64 / 10%);
}