.auth {
  background-color: #202020;
  font-family: inter;
  display: flex;
  flex-direction: column;
  padding: 70px 0;
  align-items: center;
  height: 100%;
}

.auth__form {
  width: 81%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-title {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  color: #FFFFFF;
}

.form-title_type_auth {
  padding: 40px 0;
  text-align: left;
}

.form-lable_type_auth {
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  color: #8B8B8B;
  padding-top: 24px;
  flex-direction: column;
  padding-bottom: 10px;
  min-width: 396px;
  font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #8B8B8B;
}

.form-lable_visibility_hidden {
  display: none;
  font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 24px;
    color: white;
}

.form-lable_type_auth:first-of-type {
  padding-top: 0;
  font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #8B8B8B;
}

.auth__form__input {
  border: none;
  background-color: transparent;
  padding: 5px 0 0 0;
  text-align: left;
  color: #FFFFFF;
  min-width: 100%;
  outline: none;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
}

.auth__form__input::selected{
  border: none;
  background-color: transparent;
}

input:invalid {
  color: #EE3465;
  border-bottom: 1px solid grey;
  background-color: transparent;
}

input {
  background-color: transparent;
    border-bottom: 1px solid grey;
}


.auth__error {
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  color: #EE3465;
  padding-top: 6px;
}

.auth__error_hidden {
  display: none;
}

.auth__submit-btn {
  border: none;
  background: #4285F4;
  border-radius: 3px;
  width: 383px;
  color: white;
  height: 45px;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 16px;
  cursor: pointer;
}

.getChekbox {
  display: flex;
  align-items: center;
  margin-bottom: 19px;
}

.getChekbox:first-of-type {
  margin-top: 20px;
}

#how-get {
  cursor: pointer;
  appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid #c5c5c5;
  box-sizing: border-box;
  border-radius: 10px;
}

.input {
  background-color: transparent;
  border: 1px solid grey;
  border-radius: 7px;
  height: 17px;
  padding: 5px;
  cursor: pointer;
  display: flex;
    align-items: center;
    justify-content: space-between;
} 


#how-get:checked {
  border: 6px solid #7671DD;
}

.check {
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 24px;
    margin-left: 14px;
    color: white;
}


.auth__submit-btn:hover {
  transform: scale(1.05);
  transition: all 0.4s;
}

.auth__submit-btn:disabled {
  opacity: 0.5;
  cursor: default;
}

.auth__submit-btn_type_register:disabled {
  opacity: 0.3;
}

.auth__submit-btn_type_register {
  margin-top: 134px;
}

.auth__submit-btn_type_login {
  margin-top: 204px;
}

.auth__redirect-link__title {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #8B8B8B;
  padding-right: 6px;
  cursor: default;
}

.auth__redirect-link {
  width: fit-content;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #4285F4;
  text-decoration: none;
  cursor: pointer;
}

.left {
  margin-right: 20px;
  min-width: 45%;
}

.auth__redirect-link:hover {
  opacity: 0.3;
}

.hide {
  display: none;
}

label {
  font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 24px;
    color: white;
}

.dateTitle {
  font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #8B8B8B;
    margin-right: 10px;
}

.littersWrap_Disabled {
  display: none;
}

.littersWrap {
  display: block;
  margin-bottom: 10px;
  width: 70%;
}

.date_input {
  border: 1px solid grey;
  border-radius: 7px;
  height: 17px;
  margin-right: 5px;
  padding: 5px;
  color: white;
}

.cor_input {
  border: 1px solid grey;
  border-radius: 7px;
  height: 17px;
  margin-right: 5px;
  padding: 5px;
  color: white;
  max-width: 100px;
}

.date {
  display: flex;
  align-items: center;
}

p {
  color: white;
}

@media all and (max-width: 1023px) {
  .auth {
    padding: 232px 0;
  }
}

@media all and (max-width: 575px) {
  .auth {
    padding: 56px 0 30px 0;
  }
  .auth__form {
    min-width: 260px;
    display: flex;
    flex-direction: column;
  }
  .form-lable_type_auth {
    min-width: 260px;
  }
  .auth-logo {
    max-width: 38px;
    align-self: center;
  }
  .form-title_type_auth {
    padding: 80px 0 50px 0;
  }
  .auth__redirect-link {
    font-size: 12px;
    line-height: 15px;
  }
  .auth__redirect-link__title {
    font-size: 12px;
    line-height: 15px;
  }
}