body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.d-scrollbar {
  scrollbar-color: #e2e5f1; /* #7671dd #f5f7ff */ /* «цвет ползунка» «цвет полосы скроллбара» */
  /* scrollbar-width: thin; */
}

/* полоса прокрутки (скроллбар) */
.d-scrollbar ::-webkit-scrollbar {
  /* height: 20px; */
  width: 20px; /* ширина для вертикального скролла */
  /* background-color: white;  */
  border: 1px solid black;
}

.d-scrollbar ::-webkit-scrollbar-track {
  background-color: white;
  /* width: 10px;
height: 10px;
margin: 10px 10px; */
}

/* ползунок скроллбара */
.d-scrollbar ::-webkit-scrollbar-thumb {
  /* width: 5px;*/
  /* margin: 10px; */
  cursor: pointer;
  background-color: #e2e5f1;
  border: 6px solid transparent; /* #7671dd */
  border-radius: 10px;
  background-clip: content-box;
}

/* Стрелки */

.d-scrollbar ::-webkit-scrollbar-button:vertical:start:decrement {
  display: none;
}

.d-scrollbar ::-webkit-scrollbar-button:vertical:end:increment {
  display: none;
}

.d-scrollbar ::-webkit-scrollbar-button:horizontal:start:decrement {
  display: none;
}

.d-scrollbar ::-webkit-scrollbar-button:horizontal:end:increment {
  display: none;
}

